<template>
    <header :class="{'hidden-header': !isOpenHeader && !isHomePage}">
        <div class="person-info">
            <div class="person-info__left">
                <div class="person-info__image">
                    <router-link :to="{name: 'home'}">
                        <img src="@/assets/images/me.jpg" alt="photo">
                    </router-link>
                </div> 

                <div class="person-info__info">         
                    <h1>Мартынов<br>Александр</h1>
                    <h2>web-разработчик<br><small>(без опыта)</small></h2>
                    <p>г.Ульяновск</p>
                </div>
            </div>
            <contact-list class="person-info__right"/>            
        </div>
        <skill-list />
    </header> 
    <div 
        v-if="!isHomePage" 
        class="header-tab"
        @click="isOpenHeader = !isOpenHeader"
    >
        <p v-if="!isOpenHeader">Показать инфо</p>
        <p v-if="isOpenHeader">Скрыть инфо</p>
    </div> 
</template>

<script>
import ContactList from  '@/components/ContactList.vue';
import SkillList from '@/components/SkillList.vue';

export default {
    name: 'AppHeader',
    components: {
        ContactList,
        SkillList,
    },
    computed: {
        isHomePage() {
            if(this.$route.name === 'home') {
                console.log('true')
                return true;
            } else {
                console.log('false')
                return false;
            }
        }
    },
    data() {
        return {
            isOpenHeader: false
        }
    },
    watch:{
        $route (to, from){
            if(to != from) {
                this.isOpenHeader = false;
            }
        }
    }    
}
</script>

<style lang="scss" scoped>

header {
    margin-top: 0;
    transition: 0.5s;

    &.hidden-header {
        margin-top: -255px;
        transition: 0.5s;
        @media (max-width: $mobile-max) {
            margin-top: -515px;        
        }            
    } 
}

.person-info {
    background-color: $zian;
    color: $beige;
    padding: 24px 128px;
    display: flex;   
    justify-content: space-between;
    border-bottom: 1px solid rgb(75, 91, 112);
    @media (min-width: $desktop-min) and (max-width: $desktop-max) {
        padding: 24px 64px;
    }     
    @media (min-width: $tablet-min) and (max-width: $tablet-max) {
        padding: 24px 64px;
    }      
    @media (max-width: $mobile-max) {
        border-bottom: none;        
        padding: 16px 16px 0;
        flex-direction: column;        
    }  

}
.header-tab {
    background-color: $zian;
    color: greenyellow;  
    padding: 4px 128px; 
    font-size: 12px;      
    border-bottom: 1px solid rgb(75, 91, 112);
    cursor: pointer;
    @media (min-width: $desktop-min) and (max-width: $desktop-max) {
        padding: 4px 64px;
    }     
    @media (min-width: $tablet-min) and (max-width: $tablet-max) {
        font-size: 14px;         
        padding: 16px 64px;
    }      
    @media (max-width: $mobile-max) {  
        font-size: 14px;           
        padding: 16px;      
    }        
}
.person-info__left {
    display: flex; 
    @media (max-width: $mobile-max) {
        border-bottom: 1px solid grey;
        margin-bottom: 8px;
    }     
}
.person-info__image {   
    height: 160px;
    margin-right: 24px;
    @media (max-width: $mobile-max) {       
        margin-bottom: 16px;
    } 
    img {
        height: 100%;
        border: 1px solid white;       
    }
}
.person-info__info {
    h1 {
        font-size: 24px;
        font-weight: 400;
        margin-bottom: 16px;
        letter-spacing: 1.5px;       
    }
    h2 {
        font-size: 20px;        
        font-weight: 400;
        color: yellow;
        margin-bottom: 16px;
        @media (max-width: $mobile-max) {
            font-size: 16px;  
        }         
    }
    small {
        font-weight: 300;
        font-size: 14px;
        color: white;   
        @media (max-width: $mobile-max) {
            font-size: 12px;  
        }               
    }
    p {
        position: relative;
        font-size: 16px;        
        font-weight: 400;
        padding-left: 22px;
        @media (max-width: $mobile-max) {
            font-size: 16px;  
        }        
    }  
    p::before {
        content: '';
        position: absolute;
        background-repeat: no-repeat;
        width: 24px;
        height: 24px;
        background-image: url("../assets/images/icons/location.svg");        
        background-size: 65%;
        left: 0;
        top: 3px;
    }       
}
</style>